@use 'base/mixins' as m;
@use 'base/variables' as v;

.hero{
    min-height: 40rem;
    @include m.bg-url-degraded('../img/header.jpg','../img/header.jpg','../img/header.webp','../img/header.webp','../img/header.avif','../img/header.avif' );
    background-size: cover;
    background-position: center;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: v.$white;

    &__link{
        @include m.btn(v.$tertiary, v.$white ) ;
    }
}


