@use 'base/mixins' as m;
@use 'base/variables' as v;

.categories{
    background-color: v.$ligthGray;
    border-bottom: 1px solid black;
    &__title{
        text-align: center;
        margin: 0;
        margin-bottom: 1rem;
        color: v.$black;
    }
    &__grid{
        @include m.grid(1, 2rem ) ;
        @include m.tablet {
            @include m.grid(3,2rem ) ;
        }
    }
}

.category{
    
    &__img{
        margin: 0 auto;
        height: 35rem;
        width: 80%;
        @include m.tablet {
            margin: 0;
            width: 100%;

        }
    }

    &__name{
        text-align: center;
        font-size: 2rem;
        font-weight: 700;

    }
}