@use 'base/mixins' as m;
@use 'base/variables' as v;

.search{
    background-color: darken(v.$ligthGray, 10%);
    padding:5rem 0;

    &__title{
        text-align: center;
        margin: 0;
        color: v.$gray;
    }
}

.form{
    margin-top: 1rem;
    display: flex;
    flex-direction: column;
    @include m.tablet {
        @include m.grid(4, 1rem) ;
    }

    &__field{
        padding: 1rem;
        border: 1px solid lighten(v.$gray, 50%);
        margin-bottom: 1rem;
        @include m.tablet {
            margin-bottom: 0;
        }
        
    }

    &__submit{
        margin-top: 0;
        @include m.btn(v.$secondary, v.$white ) ;

    }
}