@use 'base/mixins' as m;
@use 'base/variables' as v;

.navbar{
    display: flex;
    flex-direction: column;
    align-items: center;

    @include m.tablet {
        flex-direction: row;
        gap: 2rem;
    }

    &__link{
        margin-bottom: 2rem;
        font-size: 1.8rem;
        display: flex;
        align-items: center;
        transition: 0.3s;

        @include m.tablet {
            margin-bottom: 0;
        }

        &:last-of-type{
            margin-bottom: 0;
        }
        &--create{
            color: v.$primary;
            font-weight: 700;

            @include m.tablet {
                &::after{
                    content: '';
                    display: inline-block;
                    height: 4rem;
                    border-right: 2px solid darken(v.$ligthGray, 50%);
                    margin: 0 0 0 2rem;

                }
            }
        }
        &:hover{
            opacity: 0.5;
        }
    }
}