@use 'base/mixins' as m;
@use 'base/variables' as v;

.meet{
    
    &__name{
        text-align: center;
    }
    &__grid{
        display: grid;
        @include m.grid(1,2rem ) ;
        @include m.tablet {
            grid-template-columns: 2fr 1fr;
        }
    }
    &__text{
        font-size: 2rem;
    }
    &__info{
        font-size: 2rem;
        font-weight: 400;
    }
}

.side{
    text-align: center;
    background-color: lightblue;

    &__text{
    margin:  0;
    font-size: 2rem;
   }

    @include m.tablet {
        height: 45rem;
    }
    
    &__label{
        font-weight: 700;
    }
    &__map{
        width: 100%;
        display: block;
    }
}