html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Edu SA Beginner", cursive;
  font-size: 1.6rem;
  /*     &.body-scroll{
          padding-top: 8.6rem;
      } */
}
body.fix-body {
  overflow: hidden;
}

p {
  color: #000000;
  font-size: 1.6rem;
  line-height: 2;
}

a {
  text-decoration: none;
  color: #000000;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.container {
  width: min(90%, 120rem);
  margin: 0 auto;
}

[class$=container],
.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.section {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .section {
    padding: 8rem 0;
  }
}

.header {
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__img {
  width: 10rem;
}

.navbar {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .navbar {
    flex-direction: row;
    gap: 2rem;
  }
}
.navbar__link {
  margin-bottom: 2rem;
  font-size: 1.8rem;
  display: flex;
  align-items: center;
  transition: 0.3s;
}
@media (min-width: 768px) {
  .navbar__link {
    margin-bottom: 0;
  }
}
.navbar__link:last-of-type {
  margin-bottom: 0;
}
.navbar__link--create {
  color: #d96a2a;
  font-weight: 700;
}
@media (min-width: 768px) {
  .navbar__link--create::after {
    content: "";
    display: inline-block;
    height: 4rem;
    border-right: 2px solid #737373;
    margin: 0 0 0 2rem;
  }
}
.navbar__link:hover {
  opacity: 0.5;
}

.hero {
  min-height: 40rem;
  background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url("../img/header.jpg");
  background-size: cover;
  background-position: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #FFFFFF;
}
.webp .hero {
  background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url("../img/header.webp");
}
.avif .hero {
  background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url("../img/header.avif");
}
@media screen and (-webkit-min-device-pixel-ratio: 2), screen and (min-resolution: 192dpi), screen and (min-resolution: 2dppx) {
  .hero {
    background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url("../img/header.jpg");
  }
  .webp .hero {
    background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url("../img/header.webp");
  }
  .avif .hero {
    background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url("../img/header.avif");
  }
}
.hero__link {
  background-color: #0ca819;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: 0.3s ease;
  border: none;
}
.hero__link:hover {
  cursor: pointer;
  opacity: 0.5;
  color: gray;
}
@media (min-width: 768px) {
  .hero__link {
    width: auto;
    display: inline-block;
  }
}

.search {
  background-color: #d9d9d9;
  padding: 5rem 0;
}
.search__title {
  text-align: center;
  margin: 0;
  color: #484349;
}

.form {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .form {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1rem;
  }
}
.form__field {
  padding: 1rem;
  border: 1px solid #c7c3c8;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .form__field {
    margin-bottom: 0;
  }
}
.form__submit {
  margin-top: 0;
  background-color: #c90cd3;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: 0.3s ease;
  border: none;
}
.form__submit:hover {
  cursor: pointer;
  opacity: 0.5;
  color: gray;
}
@media (min-width: 768px) {
  .form__submit {
    width: auto;
    display: inline-block;
  }
}

.events {
  padding: 5rem 0;
}
.events__title {
  text-align: center;
  margin: 0;
  color: #484349;
}
.events__grid {
  margin-top: 2rem;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .events__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.event {
  border: 1px solid #F2F2F2;
}
.event__content {
  padding: 2rem;
}
.event__date {
  font-size: 1.4rem;
  color: #484349;
}
.event__name {
  color: #484349;
  margin: 1rem 0;
}
.event__author {
  display: flex;
  align-items: center;
  gap: 2rem;
  margin-top: 2rem;
}
.event__author-img {
  width: 10rem;
  border-radius: 50%;
  overflow: hidden;
}
.event__organized-text, .event__organized-name {
  margin: 0.5rem 0;
}
.event__organized-text {
  font-weight: 700;
}
.event__img {
  width: 100%;
  height: 30rem;
}

.categories {
  background-color: #F2F2F2;
  border-bottom: 1px solid black;
}
.categories__title {
  text-align: center;
  margin: 0;
  margin-bottom: 1rem;
  color: #000000;
}
.categories__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .categories__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.category__img {
  margin: 0 auto;
  height: 35rem;
  width: 80%;
}
@media (min-width: 768px) {
  .category__img {
    margin: 0;
    width: 100%;
  }
}
.category__name {
  text-align: center;
  font-size: 2rem;
  font-weight: 700;
}

.groups {
  background-image: linear-gradient(to bottom, #d96a2a 50%, #c90cd3 50%);
  color: #FFFFFF;
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .groups {
    background-image: linear-gradient(to right, #d96a2a 50%, #c90cd3 50%);
  }
}
.groups__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 5rem;
}
@media (min-width: 768px) {
  .groups__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.groups__title {
  font-size: 3rem;
}
.groups__text {
  color: #FFFFFF;
  line-height: 2;
  font-size: 2.4rem;
}

.footer {
  padding-top: 5rem;
}
.footer__rigths {
  padding: 1rem;
  text-align: center;
}

.meet__name {
  text-align: center;
}
.meet__grid {
  display: grid;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .meet__grid {
    grid-template-columns: 2fr 1fr;
  }
}
.meet__text {
  font-size: 2rem;
}
.meet__info {
  font-size: 2rem;
  font-weight: 400;
}

.side {
  text-align: center;
  background-color: lightblue;
}
.side__text {
  margin: 0;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .side {
    height: 45rem;
  }
}
.side__label {
  font-weight: 700;
}
.side__map {
  width: 100%;
  display: block;
}