@use 'base/mixins' as m;
@use 'base/variables' as v;

.events{
    padding: 5rem 0;


    &__title{
        text-align: center;
        margin: 0;
        color: v.$gray;
    }
    &__grid{
        margin-top: 2rem;
        @include m.grid(1,2rem ) ;
        @include m.tablet {
            @include m.grid(3, 2rem ) ;
        }
    }
}
.event{
    border: 1px solid v.$ligthGray;
    &__content{
        padding: 2rem;
    }
    &__date{
        font-size: 1.4rem;
        color: v.$gray;

    }
    &__name{
        color: v.$gray;
        margin: 1rem 0;
    }
    &__author{
        display: flex;
        align-items: center;
        gap: 2rem;
        margin-top: 2rem;
    }
    &__author-img{
        width: 10rem;
        border-radius: 50%;
        overflow: hidden;
    }
    &__organized-text,
    &__organized-name{
        margin: .5rem 0;
    }
    &__organized-text{
        font-weight: 700;
    }
    &__img{
        width: 100%;
        height: 30rem;
    }
}