@use 'base/mixins' as m;
@use 'base/variables' as v;

.groups{
    background-image: linear-gradient(to bottom, v.$primary 50%, v.$secondary 50%);
    color: v.$white;
    padding: 5rem 0;

    @include m.tablet {
        background-image: linear-gradient(to right, v.$primary 50%, v.$secondary 50%);
    }

    &__grid{
        @include m.grid(1, 5rem ) ;
        @include m.tablet {
            @include m.grid(2, 5rem ) ;
        }
    }
    &__title{
        font-size: 3rem;
    }
    &__text{
        color: v.$white;
        line-height: 2;
        font-size: 2.4rem;
    }
}