@use 'base/mixins' as m;
@use 'base/variables' as v;

.header{
    padding: 2rem 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    @include m.tablet {
        flex-direction: row;
        justify-content: space-between;
    }
    &__img{
        width: 10rem;
    }
}